<template>
  <div
    style="
      padding: var(--body-padding-top) var(--body-padding-side) 0
        var(--body-padding-side);
    "
  >
    <div class="d-flex mb-3 align-items-center">
      <div style="width: 22%; margin-left: 2px">
        <v-autocomplete
          density="compact"
          variant="outlined"
          label=" "
          :items="searchUsers"
          item-title="full_name"
          item-value="full_name"
          no-data-text=" "
          v-model="filters.search"
          :clearable="true"
          multiple
        />
      </div>
      <div class="ml-3" style="width: 18%">
        <v-select
          multiple
          chips
          density="compact"
          variant="outlined"
          label=""
          no-data-text=" "
          :items="groups"
          item-title="value"
          item-value="id"
          v-model="filters.groups"
          :clearable="true"
        />
      </div>
      <div class="ml-3" style="width: 18%">
        <v-select
          multiple
          chips
          density="compact"
          variant="outlined"
          label=""
          no-data-text=" "
          :items="streams"
          item-title="value"
          item-value="id"
          v-model="filters.streams"
          :clearable="true"
        />
      </div>
      <div class="ml-3" style="width: 15%">
        <v-select
          multiple
          chips
          density="compact"
          variant="outlined"
          label=" "
          no-data-text=" "
          :items="types"
          item-title="name"
          item-value="id"
          v-model="filters.types"
          :clearable="true"
        />
      </div>
      <div
        class="ml-3"
        style="width: 18%; align-content: center; font-size: 1.2rem"
        v-if="userVacationDaysCount"
      >
        <span>{{ `${userVacationDaysCount}  ` }}</span>
      </div>
      <v-spacer />
      <div class="d-flex align-items-center">
        <EventSettingsMenu v-model:opened="settingsMenu" />
        <v-btn
          class="btn-primary ml-2"
          @click="
            async () => {
              modal.item = null;
              modal.opened = true;
              modal.sameItems = getSameItems();
            }
          "
        >
           
        </v-btn>
      </div>
    </div>
    <v-divider />
    <EventsCalendarComponent
      class="events-calendar"
      :events="filteredEvents"
      @handleModal="handleModal"
    />
    <EventCalendarModal
      v-if="modal.opened"
      v-model:open="modal.opened"
      :item="modal.item"
      :sameItems="modal.sameItems"
      :users="users"
      :canEdit="modal.canEdit"
      :types="types"
      :vacationTypes="vacationTypes"
      @updateList="getEvents"
    />
  </div>
</template>

<script>
import EventCalendarModal from "@/components/EventsComponents/EventModal.vue";
import EventsCalendarComponent from "@/components/EventsComponents/EventsComponent.vue";
import { request } from "@/http";
import moment from "moment";
import { getDurationName } from "@/utils";
import EventSettingsMenu from "@/components/EventsComponents/EventSettingsMenu.vue";

export default {
  name: "EventsCalendar",
  components: {
    EventSettingsMenu,
    EventsCalendarComponent,
    EventCalendarModal,
  },
  async beforeMount() {
    const filters = JSON.parse(localStorage.getItem("filters"));
    if (filters) {
      this.filters = filters;
      if (!this.filters.streams) {
        this.filters.streams = [];
      }
    }
  },
  async created() {
    await this.getEvents();
  },
  data() {
    return {
      settingsMenu: false,
      vacationTypes: [
        { id: 1, name: " ", model: "in_schedule" },
        { id: 2, name: "", model: "rescheduled" },
      ],
      userVacationDaysCount: 0,
      filters: {
        groups: [],
        streams: [],
        types: [],
        search: [],
      },
      modal: {
        item: null,
        opened: false,
        canEdit: false,
        sameItems: [],
      },
      groups: [],
      streams: [],
      types: [],
      users: [],
      searchUsers: [],
      events: [],
      user: {
        user_id: null,
        team: null,
        stream: null,
        user_role: null,
      },
    };
  },
  computed: {
    filteredEvents() {
      let result = this.events;
      if (this.filters.search.length) {
        result = result.filter((i) => {
          if (this.filters.search.includes(i.extendedProps.user)) return i;
        });
      }
      if (this.filters.groups.length > 0) {
        result = result.filter((i) => {
          if (this.filters.groups.includes(i.extendedProps.stream.team_name))
            return i;
        });
      }
      if (this.filters.streams.length > 0) {
        result = result.filter((i) => {
          if (this.filters.streams.includes(i.extendedProps.stream.stream_name))
            return i;
        });
      }
      if (this.filters.types.length) {
        result = result.filter((i) => {
          if (this.filters.types.includes(i.extendedProps.type.id)) return i;
        });
      }
      localStorage.setItem("filters", JSON.stringify(this.filters));
      this.countUserVacations();
      return result;
    },
    vacationTypeID() {
      const type = this.types.find((i) => i.name === "");
      return type ? type.id : 0;
    },
  },
  methods: {
    countUserVacations() {
      this.userVacationDaysCount = 0;
      if (
        this.filters.search.length !== 1 ||
        !this.filters.types.includes(this.vacationTypeID)
      ) {
        return;
      }
      const nowYear = moment().year();
      this.events
        .filter((i) => {
          if (
            i.extendedProps.type.id === this.vacationTypeID &&
            i.extendedProps.user === this.filters.search[0]
          )
            return i;
        })
        .map((i) => {
          const start = moment(i.start);
          const end = moment(i.end);
          if (start.year() === nowYear && end.year() === nowYear) {
            this.userVacationDaysCount += moment
              .duration(end.diff(start))
              .asDays();
          }
        });
      this.userVacationDaysCount = getDurationName(this.userVacationDaysCount);
    },
    getSameItems(modalItem = null) {
      const group = this.user.team;
      if (modalItem !== null) {
        return this.events
          .filter((i) => i.id !== modalItem.id)
          .filter((i) => i.extendedProps.stream.team_id === group);
      } else {
        return this.events.filter(
          (i) => i.extendedProps.stream.team_id === group
        );
      }
    },
    async handleModal(item) {
      const modalItem = this.events.find((i) => i.id.toString() === item.id);
      this.modal.item = modalItem;
      this.modal.sameItems = this.getSameItems(modalItem);
      this.modal.opened = true;
      this.modal.canEdit = this.getPermission(modalItem);
    },
    async getEvents() {
      const userResponse = await request("api/users/info/");
      const user = {
        user_id: userResponse.id,
        team: userResponse.stream_team.team_id,
        stream: userResponse.stream_team.stream_id,
        user_role: userResponse.stream_team.stream_role,
      };
      this.user = user;

      this.searchUsers = [];
      const response = await request("api/events/");
      this.groups = response.teams;
      this.streams = response.streams;
      this.types = response.types;
      this.users = [];
      this.events = response.events.map((i) => {
        this.searchUsers.push(i.user);
        const vacationType =
          this.vacationTypes.find((j) => j.id === i.vacation_type) ?? null;
        return {
          id: i.id,
          title: `[${i.type.name}] ${i.user}`,
          start: i.start,
          end: moment(i.end).add(1, "days").format("YYYY-MM-DD"),
          extendedProps: {
            user: i.user,
            user_id: i.user_id,
            stream: {
              stream_id: i.stream_team?.stream_id,
              team_id: i.stream_team?.team_id,
              team_name: i.stream_team?.team_name,
              stream_name: i.stream_team?.stream_name,
            },
            groups: i.groups,
            type: i.type,
            description: i.description,
            transferURL: i.url,
            end: i.end,
            vacationType: vacationType,
          },
          backgroundColor: vacationType
            ? this.types.find((j) => j.name === "").additional_info[
                vacationType.model
              ] ?? i.type.background_color
            : i.type.background_color,
          textColor: i.type.text_color,
        };
      });

      const profileRole = this.user.user_role;
      const profileStreamId = this.user.stream;
      const profileTeamId = this.user.team;

      response.users.forEach((i) => {
        if (
          (profileRole === "stream_leader" &&
            profileStreamId === i.stream_team.stream_id) ||
          (profileRole === "team_leader" &&
            profileTeamId === i.stream_team.team_id) ||
          this.user.user_id === i.id
        ) {
          let user = { full_name: i.full_name, id: i.id };
          this.users.push(user);
        }
      });
      this.users.sort();
      this.searchUsers = [...new Set(this.searchUsers)];
      this.searchUsers.sort();
    },
    async getProfile() {},
    getPermission(modalItem) {
      if (this.user.user_role === "user" || this.user.user_role === null) {
        if (this.user.user_id === modalItem.extendedProps.user_id) {
          return true;
        } else {
          return false;
        }
      } else {
        if (
          this.user.stream === modalItem.extendedProps.stream.stream_id &&
          this.user.user_role === "stream_leader"
        ) {
          return true;
        } else if (
          this.user.team === modalItem.extendedProps.stream.team_id &&
          this.user.user_role === "team_leader"
        ) {
          return true;
        }
        return false;
      }
    },
  },
};
</script>

